import type { MessageEntity } from '../../../Client/Managers/MessagesManager'

import { Builder } from '../../Builder'

export interface LinkPreviewOptions {
    is_disabled?: boolean
    url?: string
    prefer_small_media?: boolean
    prefer_large_media?: boolean
    show_above_text?: boolean
}

export interface InputTextMessageContentPayload {
    message_text: string
    parse_mode?: string
    entities?: MessageEntity[]
    link_preview_options?: LinkPreviewOptions
}

export class InputTextMessageContent extends Builder<InputTextMessageContentPayload> {

    public constructor() {
        super({ message_text: '' })
    }

    public messageText(text: string): this {
        this.packet.message_text = text
        return this
    }

    public parseMode(mode?: string): this {
        this.packet.parse_mode = mode
        return this
    }

    public entities(entities?: MessageEntity[]): this {
        this.packet.entities = entities
        return this
    }

    public linkPreviewOptions(options?: LinkPreviewOptions): this {
        this.packet.link_preview_options = options
        return this
    }
    
}
